body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
p {
  margin: 0;
}

body {
  background-color: rgb(0, 121, 191);
}

.app {
  display: flex;
}

.header h1 {
  display: flex;
  align-items: center;
  color: #fff;
  height: 80px;
  margin-left: 16px;
}

.list {
  background-color: #dfe3e6;
  width: 200px;
  margin-left: 16px;
  min-width: 218px;
  padding: 16px 8px 0 8px;
  border-radius: 4px;
  max-height: 762px;
  overflow-y: auto;
  overflow-x: auto;
}

.list h2 {
  margin-bottom: 16px;
}

.card {
  background-color: #fff;
  border-radius: 4px;
  padding: 8px;
}

.card:not(:first-child) {
  margin-top: 8px;
}

.card div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card div button {
  height: 1.6rem;
  border-radius: 4px;
  cursor: pointer;
}

.list-add-card {
  margin-top: 16px;
  border-radius: 4px;
  outline: none;
  border: none;
  font-weight: 700;
  background: none;
  cursor: pointer;
}
